*{
    margin: 0px;
    padding: 0px;
    text-decoration: none;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: 1px;
    scroll-behavior: smooth;

}

body{
    width: 100%;
    height: 100%;
    background-color: white;;
    background-position: center center;
    background-size: 100%;
}

#field{
    width: 100%;
    height: 100%;
}

.app{
    width: 100%;
    height: 100%;
}

.header{
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    box-shadow: 0px 8px 16px rgba(0,0,0,0.3);
    background-color: rgb(245, 245, 245);
    position:fixed;
}

.header .top{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 5px;
    background-color: rgb(1, 26, 54);
}

.header .top span{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 12px;
    padding: 3px 10px;
    gap: 5px;
    color: white;
}

.header .top span img{
    width: 15px;
    height: 15px;
    object-fit: contain;
}

.header .top span b{
    font-size: 13px;
    color: white;
}

.header .top span a{
    color: white;
}

.header .second{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 30px;
}

.header .second img{
    width: 100px;
    height: 70px;
    object-fit: contain;
}

.header .second nav{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;
}

.header .second nav a{
    font-size: 18px;
    color: rgb(1, 26, 54);
    letter-spacing: 2px;
    transition: 1s;
}

.header .second nav a:hover{
    color: rgb(218, 177, 43);
    border-bottom: 1px solid rgb(218, 177, 43);
}

.header .second button{
    display: none;
}

.main{
    width: 100%;
    height: calc(100% - 260px);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.main section{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    margin-top: 100px;
}

.main .home{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.main .home .find{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 15px 30px;
    gap: 15px;
}

.main .home .find h2{
    font-size: 28px;
    text-align: center;
    color: rgb(1, 26, 54);
}

.main .home .find p{
    font-size: 18px;
    text-align: center;
    font-style: italic;
    gap: 2px;
    letter-spacing: 2px;
    color:black;
}

.main .home .house{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 10px 50px;
    box-shadow: 0px 8px 16px rgba(0,0,0,0.1);
}

.main .home .house img{
    width: 430px;
    height: 500px;
    object-fit: contain;
}

.main .home .drive{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    z-index: -1;
}

.main .home .drive span{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    position: relative;
}

.main .home .drive span h2{
    font-size: 30px;
    color: black;
    align-self: flex-start;
}

.main .home .drive span .list{
    width: fit-content;
    height: fit-content;
    gap: 10px;
}

.main .home .drive span .list li{
    color: grey;
    font-size: 17px;
}

.main .home .drive span img{
    width: 550px;
    height: 350px;
    object-fit: contain;
    border-radius: 100px 0px 100px 0px;
}

.main .home .drive span small{
    width: 50px;
    height: 100px;
    background-color: darkorange;
    position: absolute;
    right: 50px;
    top: 0px;
    transform: translate(0px);
    box-shadow: 0px 8px 16px rgba(0,0,0,0.3);
}

.main .home .team{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.main .home .team h2{
    font-size: 40px;
    color: rgb(1, 26, 54);
    letter-spacing: 5px;
}

.main .home .team div{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 40px;
    flex-wrap: wrap;
    overflow-x: auto;
    padding: 10px 30px;
}

.main .home .team div span{
    width: 620px;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(155, 121, 11);
    border-radius: 25px;
    padding: 10px 10px;
    
}

.main .home .team div span small{
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    color: white;
    gap: 5px;
    padding: 10px 20px;
}

.main .home .team div span img{
    width: 200px;
    height: 200px;
    object-fit: contain;
}

.main .home .team div span small b{
    font-size: 18px;
}

.main .home .team div span small i{
    font-size: 14px;
    letter-spacing: 2px;
}

.main .home .cert{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    padding: 10px 30px;
}

.main .home .cert h2{
    font-size: 40px;
    color: black;
}

.main .home .cert div{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 50px 0px;
    gap: 10px;
    flex-wrap: wrap;
    overflow-x: auto;
    background-color: rgb(218, 177, 43);
}

.main .home .cert div span img{
    width: 410px;
    height: 400px;
    object-fit: initial;
    align-self: center;
    padding-left: 50px;
}

.main .home .cert div .certi{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-self: center;
    align-items: center;
    padding-left: 150px;
}

.main .home .cert div .certi img{
    width: 1000px;
    height: 500px;
    object-fit: initial;
}

.main .real{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 10px 30px;
}

.main .real h2{
    font-size: 40px;
    letter-spacing: 3px;
    color: rgb(1, 26, 54);
}

.main .real .estate{
    width: 100%;
    height: fit-content;
    gap: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.main .real .estate img{
    width: 650px;
    height: 400px;
    object-fit: cover;
}

.main .real p{
    font-size: 20px;
    letter-spacing: 4px;
    color: rgb(95, 93, 93);
}

.main .real .div{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
}

.main .real .div span{
    width: 100%;
    height: fit-content;
    display: flex;
    gap: 10px;
}

.main .real .div img{
    width: 410px;
    height: 300px;
    object-fit: cover;
    gap: 2px;
}

.main .smart{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 40px;
    padding: 10px 30px;
}

.main .smart h2{
    font-size: 40px;
    letter-spacing: 2px;
    color: rgb(1, 26, 54);
}

.main .smart .smart-img{
   width: 100%;
   display: flex;
   gap: 15px;
}

.main .smart .smart-img img{
    width: 420px;
    height: 400px;
    object-fit: cover;
}

.main .smart p{
    font-size: 20px;
    letter-spacing: 4px;
    color: black;
}

.main .smart .rural{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
}

.main .smart .rural h2{
    text-align: center;
}

.main .smart .rural span{
    width: 100%;
    height: fit-content;
    display: flex;
    gap: 15px;
}

.main .smart .rural img{
    width: 420px;
    height: 400px;
    object-fit: cover;
}

.main .smart .com{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 10px 20px;
    background-color: rgb(218, 177, 43);
    border-radius: 25px;
}

.main .smart .com article{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
}

.main .smart .com article img{
    width: 600px;
    height: 400px;
    object-fit: cover;
}

.main .smart .com article p{
    font-size: 25px;
}

.main .what-we-do{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 30px;
    gap: 40px;
}

.main .what-we-do h2{
    font-size: 40px;
    color: rgb(1, 26, 54);
    letter-spacing: 2px;
}

.main .what-we-do b{
    color: rgb(218, 177, 43);
    font-size: 30px;
    font-weight: 400;
    letter-spacing: 5px;
}

.main .what-we-do img{
    width: 1200px;
    height: 550px;
    object-fit: initial;
}

.main .what-we-do p{
    font-size: 20px;
    color: rgb(95, 93, 93);
    letter-spacing: 3px;
}

.main .what-we-do .hello{
    color: rgb(218, 177, 43);
}

.main .what-we-do span{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
    padding: 10px 20px;
    background-color: rgb(218, 177, 43);
}

.main .what-we-do span b{
    font-size: 25px;
    color: rgb(1, 26, 54);
    font-weight: 700;
}

.main .what-we-do span ul{
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0px 30px;
}

.main .project{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 50px;
    padding: 10px 30px;
    background-color: rgb(218, 177, 43);
}

.main .project h2{
    font-size: 40px;
    color: rgb(1, 26, 54);
    letter-spacing: 4px;
}

.main .project p{
    font-size: 20px;
    color: black;
    letter-spacing: 4px;
    text-align: center;
}

.main .project span{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 30px;
}

.main .project span small{
    width: 50%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 8px;
}

.main .project span small img{
    width: 600px;
    height: 370px;
    object-fit: initial;
}

.main .project span small b{
    font-size: 17px;
    color: rgb(1, 26, 54);
    text-align: center;
}

.main .contact{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 100px;
    padding: 10px 30px;
}

.main .contact h2{
    font-size: 30px;
    color: rgb(1, 26, 54);
    letter-spacing: 4px;
}

.main .contact .call{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
}

.main .contact .call .map{
    background-color: rgb(1, 26, 54);
    width: 650px;
    height: 400px;
    object-fit: contain;
}

.main .contact .call article{
    width: 45%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 46px;
}

.main .contact .call article p{
    font-size: 20px;
    color: rgb(218, 177, 43);
    letter-spacing: 3px;
}

.main .contact .call article h2{
    font-size: 40px;
    letter-spacing: 2px;
}

.main .contact .call article span{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transition: 1s;
}

.main .contact .call article span:hover{
    transform: scale(1.1);
    z-index: -1;
}

.main .contact .call article span img{
    width: 40px;
    height: 40px;
    object-fit: contain;
    background-color: rgb(218, 177, 43);
    padding: 10px;
    border-radius: 50%;
}

.main .contact .call article span small{
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 2px;
}

.main .contact .call article small{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transition: 1s;
}

.main .contact .call article small:hover{
    transform: scale(1.1);
    z-index: -1;
}

.main .contact .call article small img{
    width: 40px;
    height: 40px;
    object-fit: contain;
    background-color: rgb(218, 177, 43);
    padding: 10px;
    border-radius: 50%;
}

.main .contact .call article small a, b{
    font-size: 18px;
    font-weight: 600;
    color: rgb(1, 26, 54);
}

.main .contact .equip{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 30px;
}

.main .contact .equip h2{
    font-size: 40px;
    font-weight: 400;
    color: rgb(218, 177, 43);
    letter-spacing: 5px;
}

.main .contact .equip span{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    overflow-x: auto;
    padding: 10px 30px;
    gap: 10px;
}

.main .contact .equip span small{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.main .contact .equip span img{
    width: 600px;
    height: 400px;
    object-fit: initial;
}

footer{
    width: 100%;
    height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: rgba(1, 26, 54, 0.884);
    padding: 10px 0px;
}

footer article{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
}

footer article .logo{
    width: 200px;
    height: 120px;
    object-fit: cover;
    background-color: rgb(218, 177, 43);
    border-radius: 25px;
}

footer article .spans{
    width: 60%;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 50px;
}

footer article .spans span{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

footer article .spans span img{
    width: 40px;
    height: 40px;
    object-fit: contain;
    background-color: rgb(218, 177, 43);
    padding: 10px;
    border-radius: 50%;
}

footer article .spans span small{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

footer article .spans span small a{
    font-size: 18px;
    color: white;
}

footer article .spans small{
    width: fit-content;
    height: fit-content;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

footer article .spans small img{
    width: 40px;
    height: 40px;
    object-fit: contain;
    background-color: rgb(218, 177, 43);
    padding: 10px;
    border-radius: 50%;
}

footer article .spans small a{
    font-size: 18px;
    color: white;
}

footer p{
    width: 100%;
    font-size: 14px;
    color: rgb(95, 93, 93);
    letter-spacing: 2px;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 0px;
    background-color: white;
}