@media(max-width:450px){

    
    .header .top{
        display: none;
    }

    .header .second{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0px 20px;
    }
    
    .header .second img{
        width: 100px;
        height: 80px;
        object-fit: cover;
    }

    .header .second .menu{
        width: 40px;
        height: 50px;
        object-fit: contain;
        display: flex;
        transition: 1s;
    }

    .header .second .menu:hover{
        transform: scale(1.1);
        filter: grayscale(1.1);
    }
    
    .header .second nav{
        width: 250px;
        height: calc(100vh - 100px);
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 40px;
        position: absolute;
        padding: 40px 30px;
        top: 100px;
        left: -250px;
        z-index: 100;
        background-color: rgba(218, 177, 43, 0.966);
        transition: 1s;
    }

    .header .second nav a{
        font-size: 20px;
        color: rgb(1, 26, 54);
        letter-spacing: 0px;
        transition: 1s;
        font-weight: bold;
    }
    
    .header .second nav a:hover{
        color: black;
        border-bottom: 1px solid black;
        transform: scale(1.1);
    }

    .header .second button{
        display: flex;
    }
    
    .main .home .find{
        padding: 15px 10px;
    }
    
    .main .home .find h2{
        font-size: 30px;
        text-align: left;
    }
    
    .main .home .find p{
        font-size: 20px;
        gap: 1px;
        letter-spacing: 1px;
        text-align: left;
    }
    
    .main .home .house{
        flex-direction: column;
        gap: 10px;
        padding: 10px 10px;
    }
    
    .main .home .house img{
        width: 380px;
        height: 300px;
        object-fit: contain;
    }
    
    .main .home .drive{
        flex-direction: column;
        padding: 15px 10px;
        z-index: -1;
    }
    
    .main .home .drive span{
        flex-direction: column;
        gap: 10px;
    }
    
    .main .home .drive span h2{
        font-size: 28px;
        color: black;
        align-self: start;
    }
    
    .main .home .drive span .list{
        width: 100%;
        gap: 0px;
        padding: 0px 30px;
    }
    
    .main .home .drive span .list li{
        color: grey;
        font-size: 17px;
    }
    
    .main .home .drive span img{
        width: 380px;
        height: 300px;
        border-radius: 100px 0px 100px 0px;
    }
    
    .main .home .drive span small{
        height: 80px;
        right: -8px;
        top: 28px;
    }
    
    .main .home .team{
        gap: 10px;
    }
    
    .main .home .team h2{
        font-size: 30px;
        color: rgb(1, 26, 54);
        letter-spacing: 1px;
    }
    
    .main .home .team div{
        padding: 10px 10px;
        gap: 10px;
    }
    
    .main .home .team div span{
        flex-direction: column;
        padding: 10px 10px;
        border-radius: 5px;
        text-align: center;
        
    }

    .main .home .team div span b{
        text-align: center;
        align-self: center;
    }
    
    .main .home .team div span small{
        padding: 10px 0px;
    }
    
    .main .home .team div span img{
        width: 200px;
        height: 200px;
        object-fit: contain;
    }

    .main .home .cert{
        padding: 10px 0px;
    }
    
    .main .home .cert h2{
        font-size: 30px;
    }
    
    .main .home .cert div{
        padding: 10px 0px;
        gap: 10px;
        flex-wrap: wrap;
        overflow-x: auto;
    }
    
    .main .home .cert div span img{
        width: 380px;
        height: 300px;
        object-fit: initial;
        padding-left: 0px;
    }
    
    .main .home .cert div .certi{
        padding-left: 0px;
    }
    
    .main .home .cert div .certi img{
        width: 380px;
        height: 300px;
        object-fit: initial;
    }
    
    .main .real{
        padding: 10px 10px;
    }
    
    .main .real h2{
        font-size: 30px;
        letter-spacing: 1px;
        color: rgb(1, 26, 54);
    }
    
    .main .real .estate{
        flex-wrap: wrap;
        overflow-x: auto;
        gap: 0px;
    }
    
    .main .real .estate img{
        width: 380px;
        height: 300px;
        object-fit: contain;
    }
    
    .main .real p{
        font-size: 20px;
        letter-spacing: 1px;
    }
    
    .main .real .div{
        gap: 10px;
    }
    
    .main .real .div span{
        flex-wrap: wrap;
        overflow-x: auto;
        gap: 10px;
    }
    
    .main .real .div img{
        width: 400px;
        height: 300px;
        object-fit: cover;
    }
    
    .main .smart{
        padding: 10px 10px;
    }
    
    .main .smart h2{
        font-size: 30px;
        letter-spacing: 1px;
    }
    
    .main .smart .smart-img{
       flex-wrap: wrap;
       overflow-x: auto;
       gap: 10px;
    }
    
    .main .smart .smart-img img{
        width: 380px;
        height: 300px;
        object-fit: cover;
    }
    
    .main .smart p{
        font-size: 20px;
        letter-spacing: 1px;
    }
    
    .main .smart .rural{
        gap: 10px;
    }
    
    .main .smart .rural span{
        flex-wrap: wrap;
        overflow-x: auto;
        gap: 10px;
    }
    
    .main .smart .rural img{
        width: 380px;
        height: 300px;
        object-fit: cover;
    }
    
    .main .smart .com{
        padding: 10px 5px;
        border-radius: 5px;
    }
    
    .main .smart .com article{
        flex-direction: column;
        gap: 20px;
    }
    
    .main .smart .com article img{
        width: 350px;
        height: 300px;
        border-radius: 15px;
    }
    
    .main .smart .com article p{
        font-size: 20px;
    }
    
    .main .what-we-do{
        padding: 10px 10px;
        gap: 50px;
    }
    
    .main .what-we-do h2{
        font-size: 30px;
    }
    
    .main .what-we-do b{
        font-size: 25px;
        letter-spacing: 1px;
    }
    
    .main .what-we-do img{
        width: 380px;
        height: 300px;
    }
    
    .main .what-we-do p{
        font-size: 20px;
        letter-spacing: 1px;
    }
    
    .main .what-we-do span{
        padding: 10px 10px;
    }
    
    .main .what-we-do span b{
        font-size: 20px;
    }
    
    .main .what-we-do span ul{
        padding: 0px 20px;
    }
    
    .main .project{
        gap: 20px;
        padding: 10px 10px;
    }
    
    .main .project h2{
        font-size: 30px;
        letter-spacing: 1px;
    }
    
    .main .project p{
        font-size: 18px;
        letter-spacing: 1px;
        text-align: left;
    }
    
    .main .project span{
        gap: 10px;
        flex-direction: column;
        padding: 10px 10px;
    }
    
    .main .project span small{
        width: 100%;
        gap: 10px;
    }
    
    .main .project span small img{
        width: 380px;
        height: 300px;
        object-fit: initial;
    }
    
    .main .project span small b{
        font-size: 14px;
    }
    
    .main .contact{
        gap: 50px;
        padding: 10px 10px;
    }
    
    .main .contact h2{
        font-size: 25px;
        letter-spacing: 0px;
    }
    
    .main .contact .call{
        flex-direction: column;
        justify-content: center;
        padding: 10px 10px;
        gap: 20px;
    }
    
    .main .contact .call .map{
        background-color: rgba(1, 26, 54, 0.945);
        width: 380px;
        height: 300px;
        object-fit: contain;
    }
    
    .main .contact .call article{
        width: 100%;
        gap: 30px;
    }
    
    .main .contact .call article h2{
        font-size: 35px;
        letter-spacing: 2px;
    }
    
    .main .contact .call article small a, b{
        font-size: 16px;
    }
    
    .main .contact .equip h2{
        font-size: 35px;
        letter-spacing: 0px;
    }
    
    .main .contact .equip span{
        padding: 10px 10px;
    }
    
    .main .contact .equip span small{
        gap: 10px;
    }
    
    .main .contact .equip span img{
        width: 190px;
        height: 120px;
        object-fit: cover;
    }

    
    footer article{
        padding: 10px 10px;
        gap: 5px;
    }
    
    footer article .logo{
        width: 130px;
        height: 80px;
    }
    
    footer article .spans{
        width: 60%;
        flex-direction: column;
        gap: 10px;
    }
    
    footer article .spans span{
        width: 100%;
        gap: 5px;
    }
    
    footer article .spans span img{
        width: 30px;
        height: 30px;
        padding: 5px;
    }
    
    footer article .spans span small{
        width: 100%;
        flex-direction: column;
        gap: 5px;
    }
    
    footer article .spans span small a{
        font-size: 12px;
        color: white;
        align-self: flex-start;
    }
    
    footer article .spans small{
        width: 100%;
    }
    
    footer article .spans small img{
        width: 30px;
        height: 30px;
        padding: 2px;
    }
    
    footer article .spans small a{
        font-size: 12px;
        color: white;
    }
    
    footer p{
        font-size: 14px;
        text-align: center;
    }
}